export const BASE_URL = 'http://127.0.0.1:8000'
// const BASE_URL = 'http://192.168.0.15:8000'
const refresh = uni.getStorageSync('refresh')
// // exportmyRequest,myRequestoptions
export const request = (options) => {
	return new Promise((resolve, reject) => { // promisereturn
		uni.request({ // 
			url: BASE_URL + options.url, // options
			method: options.method || 'GET', // optionsmethodGET
			data: options.data || {}, // dataoptionsdata 
			header: options.headers || {},
			success: (res) => { // res
				// console.log(options.data);
				if (res.statusCode == 200) { // 
					resolve(res) //resolve
				} else if (res.statusCode == 401) {
					if (res.data.extra.token_message == "Token is invalid or expired") {
						uni.request({
							url: BASE_URL + '/api/accounts/token/',
							method: 'POST',
							data: {
								refresh: refresh
							},
							success(res) {
								console.log(res);
								uni.setStorageSync('access', res.data.access)
								console.log(uni.getStorageSync('access'));
							}
						})
					return uni.showModal({
						title: '',
						content: '~',
						success: function(res) {
							if (res.confirm) {
								console.log('');
								uni.reLaunch({

									url: '/pages/index/index'
								})
							} else if (res.cancel) {
								console.log('');
							}
						}
					})
					}
				} else if (res.statusCode == 400) {
					console.log(res);
					return uni.showToast({
						title: res.data.message,
						duration: 1500,
						icon: "none"
					})
				} else {
					return uni.showToast({
						title: '',
						duration: 1500,
						icon: "none"
					})
				}
			},
			fail: (err) => { // err 
				uni.showToast({ // 
					title: ''
				})
				reject(err) // reject
			}

		})
	})
}